import React, {ReactElement} from 'react';
import { useParams } from "react-router-dom";
import MMF from "./MMF";
import LPMining from "./LPMining";

const Farm: React.FC = () => {
  let params = useParams();
  let dataNode: ReactElement;

  switch (params.miningType) {
    case 'MMF':
      dataNode = <MMF />;
      break;
    case 'LPMining':
      dataNode = <LPMining />;
      break;
    default:
      dataNode = <MMF />;
      break;
  }
  return (
    <>
      {dataNode}
    </>
  )
};

export default Farm;
