import React, {ReactElement} from 'react';
import { useParams } from "react-router-dom";
import TiUSDData from "./TiUSDData";
import TiTiData from "./TiTiData";
import ReOrdersData from "./ReOrdersData";
import ProtocolFeeData from "./ProtocolFeeData";

const Dashboard: React.FC = () => {
  let params = useParams();
  let dataNode: ReactElement;
  switch (params.dashboardType) {
    case 'TiUSD':
      dataNode = <TiUSDData />;
      break;
    case 'TiTi':
      dataNode = <TiTiData />;
      break;
    case 'ReOrders':
      dataNode = <ReOrdersData />;
      break;
    case 'ProtocolFee':
      dataNode = <ProtocolFeeData />
      break;
    default:
      dataNode = <TiUSDData />;
      break;
  }

  return (
    <>
      {dataNode}
    </>
  )
};

export default Dashboard;
