import {AppBar, Toolbar, Box, Button, IconButton, FormControlLabel, Switch, Typography} from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import {useDispatch, useSelector} from "react-redux";
import { toggleTheme } from "../../slices/ThemeSlice";
import React from "react";
import ModalButton from "../ModalButton";
import {RootState} from "../../store";

interface ITopBarProps {
    handleDrawerToggle: () => void;
    drawerWidth: number
}

const TopBar: React.FC<ITopBarProps> = ({handleDrawerToggle, drawerWidth}) => {
  const dispatch = useDispatch();
  const theme = useSelector((state: RootState) => state.theme);
  return (
    <AppBar position="fixed" elevation={0} sx={{
      justifyContent: "space-between",
      alignItems: "flex-end",
      background: "transparent",
      backdropFilter: "none",
      width: { sm: `calc(100% - ${drawerWidth}px)` },
      ml: { sm: `${drawerWidth}px` },
      zIndex: 10,
    }}>
      <Toolbar disableGutters sx={{
        width: "100%",
        display: 'flex',
        justifyContent: 'space-between',
      }}>
        <Box display="flex" >
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ ml: 2, display: { sm: 'none' }}}
          >
            <MenuIcon />
          </IconButton>
        </Box>
        <Box display="flex" >
          <ModalButton name="Recent Transactions">Recent Transactions</ModalButton>
          <Button variant="outlined" onClick={() => {}}>Connect Wallet</Button>
          <Switch defaultChecked onClick={() => dispatch(toggleTheme())} />
          <Typography sx={{display: 'flex', alignItems: 'center'}}>
            {theme}
          </Typography>
        </Box>
      </Toolbar>
    </AppBar>
);
}

export default TopBar;
