import React, {useEffect, useState} from 'react';
import { useSelector } from 'react-redux';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { ThemeProvider } from '@mui/material/styles';
import { CssBaseline } from '@mui/material';
import {RootState} from "./store";
import {lightTheme} from "./themes/lightTheme";
import {darkTheme} from "./themes/darkTheme";
import Dashboard from "./views/Dashboard";
import Swap from "./views/Swap";
import Farm from "./views/Farm";
import Use2Earn from "./views/Use2Earn";
import PageSkeleton from "./components/PageSkeleton";
import TiTiStaking from "./views/TiTiStaking";
import MyAccount from "./views/MyAccount";

function App() {
  const theme = useSelector((state: RootState) => state.theme);
  const [themeMode, setThemeMode] = useState(theme === "light" ? lightTheme : darkTheme);

  useEffect(() => {
    window.localStorage.setItem("theme", theme);
    setThemeMode(theme === "light" ? lightTheme : darkTheme);
  }, [theme]);

  return (
    <ThemeProvider theme={themeMode}>
      <CssBaseline />
      <Router>
        <PageSkeleton>
          <Routes>
            <Route path="/" element={<Dashboard />} />
            <Route path="/swap" element={<Swap />} />
            <Route path="/farm">
              <Route path=":miningType" element={<Farm />} />
            </Route>
            <Route path="/dashboard">
              <Route path=":dashboardType" element={<Dashboard />} />
            </Route>
            <Route path="/use2earn" element={<Use2Earn />} >
              <Route path=":eventId" element={<Use2Earn />} />
            </Route>
            <Route path="/titiStaking" element={<TiTiStaking />} />
            <Route path="/myAccount" element={<MyAccount />} />
          </Routes>
        </PageSkeleton>
      </Router>
    </ThemeProvider>
  );
}

export default App;
