import * as React from 'react';
import {ListItem, ListItemProps, Collapse, Typography, ListItemText, List} from '@mui/material/';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { NavLink } from "react-router-dom";
import {ListItemIcon} from "@mui/material";
import InboxIcon from "@mui/icons-material/MoveToInbox";
import {useState} from "react";

interface ListItemLinkProps extends ListItemProps {
  name: string;
  to: string;
  dataIds: string[];
}

const NavItemLink: React.FC<ListItemLinkProps> = ({name, to, dataIds}) => {

  const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  let icon = null;
  if (open != null) {
    icon = open ? <ExpandLess /> : <ExpandMore />;
  }

  return (
    <>
      <ListItem button onClick={handleClick}>
        <ListItemIcon>
          <InboxIcon />
        </ListItemIcon>
        <ListItemText primary={name} />
        {icon}
      </ListItem>
      <Collapse component="li" in={open} timeout="auto" unmountOnExit>
        <List disablePadding sx={{ml: 3}}>
          {dataIds.map((text)=>(
            <ListItem button key={text} component={NavLink} to={to + '/' + text}>
              <ListItemIcon>
                <InboxIcon />
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
      </Collapse>
    </>
  );
}

export default NavItemLink;
